body{color:#6a5acd;font-family:Arial, Helvetica, sans-serif}#modal{opacity:0;z-index:-1;transition:opacity 400ms;background:#000000b8;position:absolute;width:100%;height:100%;top:0;left:0;background:#000000b8;display:flex;align-items:center;justify-content:center}#modal.open{opacity:1;z-index:1}#modal .content{min-width:600px;min-height:400px;background:white;border-radius:5px}#modal #close-modal-button{color:#fff;background-color:#2C3E50;border-color:#2C3E50;padding:10px 20px;font-weight:400;font-size:1em;border-radius:4px}@media (max-width: 481px){.fc-toolbar{flex-direction:column}.fc-scroller.fc-time-grid-container,.fc-scroller.fc-time-grid-container,.fc-scroller.fc-day-grid-container{height:100% !important}}
